[_c('div',{staticClass:"content text-dark",attrs:{"id":"Legal Disputes"}},[_c('h3',{staticClass:"text-lg font-weight-bold"},[_vm._v("\n                                     18. Legal Disputes\n                                    ")]),_c('p',[_c('b',[_vm._v("PLEASE READ THIS SECTION CAREFULLY. IT AFFECTS YOUR RIGHTS AND WILL HAVE A SUBSTANTIAL IMPACT ON HOW CLAIMS YOU AND REJEE Stores HAVE AGAINST EACH OTHER ARE RESOLVED.")])]),_c('p',[_c('b',[_vm._v("In this Legal Disputes Section, the term “related third parties” includes your and REJEE Stores’s respective affiliates, subsidiaries, parent companies, predecessors, successors, assigns, as well as your, REJEE Stores’s, and these entities’ respective employees and agents.")])]),_c('p',[_c('b',[_vm._v("You and REJEE Stores agree that any claim or dispute at law or equity that has arisen, or may arise, between you and REJEE Stores (or any related third parties) that relates in any way to or arises out of this or previous versions of this User Agreement, your use of or access to the Services, the actions of REJEE Stores or its agents, or any products or services sold or purchased through the Services, will be resolved in accordance with the provisions set forth in this Legal Disputes Section.")])]),_c('p')])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row product-container pb-3 pt-3"},[_c('div',{staticClass:"content text-dark"},[_c('h3',{staticClass:"text-lg font-weight-bold"},[_vm._v("\n                                     A. Applicable Law\n                                    ")]),_c('p',[_vm._v("You agree that, except to the extent inconsistent with or preempted by federal law, the laws of the State of Utah, without regard to principles of conflict of laws, will govern this User Agreement and any claim or dispute that has arisen or may arise between you and REJEE Stores, except as otherwise stated in this User Agreement.\n                                    ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row product-container pb-3 pt-3"},[_c('div',{staticClass:"content text-dark"},[_c('h3',{staticClass:"text-lg font-weight-bold"},[_vm._v("\n                                     B. Agreement to Arbitrate\n                                    ")]),_c('p',[_c('b',[_vm._v("You and REJEE Stores each agree that any and all disputes or claims that have arisen, or may arise, between you and REJEE Stores (or any related third parties) that relate in any way to or arise out of this or previous versions of the User Agreement, your use of or access to our Services, the actions of REJEE Stores or its agents, or any products or services sold, offered, or purchased through our Services shall be resolved exclusively through final and binding arbitration, rather than in court.\nAlternatively, you may assert your claims in small claims court, if your claims qualify and so long as the matter remains in such court and advances only on an individual (non-class, non-representative) basis. The Federal Arbitration Act (\"FAA\"), and to the extent not inconsistent with the FAA, the laws of the State of Utah, without regard to principles of conflict of laws, governs the interpretation and enforcement of this Agreement to Arbitrate.\n")])]),_c('ul',{staticClass:"pl-3 pt-3"},[_c('li',{staticClass:"pb-3"},[_c('b',[_vm._v("1.\tProhibition of Class and Representative Actions and Non-Individualized Relief\n                                        "),_c('br'),_vm._v("YOU AND REJEE Stores AGREE THAT EACH OF US MAY BRING CLAIMS AGAINST THE OTHER ONLY ON AN INDIVIDUAL BASIS AND NOT AS A PLAINTIFF OR CLASS MEMBER IN ANY PURPORTED CLASS, OR REPRESENTATIVE OR PRIVATE ATTORNEY GENERAL ACTION OR PROCEEDING. UNLESS BOTH YOU AND REJEE Stores AGREE OTHERWISE, THE ARBITRATOR MAY NOT CONSOLIDATE OR JOIN MORE THAN ONE PERSON'S OR PARTY'S CLAIMS, AND MAY NOT OTHERWISE PRESIDE OVER ANY FORM OF A CONSOLIDATED, REPRESENTATIVE, CLASS, OR PRIVATE ATTORNEY GENERAL ACTION OR PROCEEDING. ALSO, THE ARBITRATOR MAY AWARD RELIEF (INCLUDING MONETARY, INJUNCTIVE, AND DECLARATORY RELIEF) ONLY IN FAVOR OF THE INDIVIDUAL PARTY SEEKING RELIEF AND ONLY TO THE EXTENT NECESSARY TO PROVIDE RELIEF NECESSITATED BY THAT PARTY'S INDIVIDUAL CLAIM(S). ANY RELIEF AWARDED CANNOT AFFECT OTHER USERS. ")]),_vm._v("If a court decides that applicable law precludes enforcement of any of this paragraph's limitations as to a particular claim or a particular request for a remedy (such as a request for injunctive relief), then that claim or that remedy request (and only that claim or that remedy request) must be severed from the arbitration and may be brought in court (pursuant to Section 18.C below), subject to your and REJEE Stores's right to appeal the court's decision. All other claims will be arbitrated.")]),_c('li',{staticClass:"pb-3"},[_c('b',[_vm._v("2.\tArbitration Procedures")]),_c('br'),_vm._v("\n                                        Arbitration is more informal than a lawsuit in court. Arbitration uses a neutral arbitrator instead of a judge or jury, and court review of an arbitration award is very limited. However, an arbitrator can award the same damages and relief on an individualized basis that a court can award to an individual. An arbitrator should apply the terms of the User Agreement as a court would. All issues are for the arbitrator to decide, except that issues relating to arbitrability, the scope or enforceability of this Agreement to Arbitrate, or the interpretation of Section 1 of this Agreement to Arbitrate (\"Prohibition of Class and Representative Actions and Non-Individualized Relief\"), shall be for a court of competent jurisdiction to decide.\n                                        ")]),_c('li',[_vm._v("The arbitration will be administered by the American Arbitration Association (\"AAA\") under its rules and procedures, including the AAA's Consumer Arbitration Rules and the AAA's Commercial Arbitration Rules, and the AAA's International Centre for Dispute Resolution Rules (as applicable), as modified by this Agreement to Arbitrate. Absent agreement of the parties, the AAA shall decide which AAA rules apply to the arbitration. The AAA's rules are available at www.adr.org and www.icdr.org. In the event that the AAA is unavailable to administer the arbitration, another administrator will be selected by the parties or, if the parties cannot reach the agreement, the court (pursuant to Section 18.C below) shall select the administrator.\nA party who intends to seek arbitration must first send to the other, by certified mail, a valid Notice of Dispute (\"Notice\"), which may be downloaded at this link. The Notice to REJEE Stores must be sent to REJEE Stores Inc., Attn: Litigation Department, Re: Notice of Dispute, 583 W. REJEE Stores Way, Draper, UT 84020. REJEE Stores will send any Notice to you to the physical address we have on file associated with your REJEE Stores account; it is your responsibility to keep your physical address up to date. To be valid, you must personally sign the Notice and complete all information on the Notice form, including a description of the nature and basis of the claims you are asserting, the specific relief sought, and the email address and phone number associated with your account.\n")]),_c('li',[_vm._v("If you and REJEE Stores are unable to resolve the claims described in a valid Notice within 30 days after REJEE Stores receives that Notice, you or REJEE Stores may initiate arbitration proceedings. A form for initiating arbitration proceedings is available on the AAA's site at www.adr.org. In addition to filing this form with the AAA in accordance with its rules and procedures, the party initiating the arbitration must mail a copy of the completed form to the opposing party. You may send a copy to REJEE Stores at the following address: REJEE Stores, Inc. c/o CT Corporation System, 1108 E South Union Ave., Midvale, UT 84047. In the event REJEE Stores initiates an arbitration against you, it will send a copy of the completed form to the physical address we have on file associated with your REJEE Stores account. Any settlement offer made by you or REJEE Stores shall not be disclosed to the arbitrator.")]),_c('li',[_vm._v("If you are a resident of the United States, then the arbitration hearing shall be held in the county in which you reside or at another mutually agreed location. If you are not a resident of the United States, then the arbitration hearing will be held in Salt Lake County, Utah, United States, or another mutually agreed location. Where no disclosed claims or counterclaims exceed $25,000, the dispute shall be resolved by the submission of documents only, subject to the arbitrator's discretion to require an in-person hearing, if the circumstances warrant. In cases where an in-person hearing is held, you and/or REJEE Stores may attend by telephone, unless the arbitrator requires otherwise. The language of the arbitration will be English.")]),_c('li',[_vm._v("The arbitrator will decide the substance of all claims in accordance with applicable law, including recognized principles of equity, and will honor all claims of privilege recognized by law. The arbitrator shall not be bound by rulings in prior arbitrations involving different users, but is bound by rulings in prior arbitrations involving the same REJEE Stores user to the extent required by applicable law. The arbitrator's award shall be final and binding and judgment on the award rendered by the arbitrator may be entered in any court having jurisdiction thereof.")]),_c('li',{staticClass:"pb-3"},[_c('b',[_vm._v("3.\tCosts of Arbitration")])]),_c('li',[_vm._v("Payment of all filing, administration and arbitrator fees will be governed by the AAA's rules, unless otherwise stated in this Agreement to Arbitrate. If you complied with the Notice of Dispute procedures of Section 2 of this Agreement (\"Arbitration Procedures\") and the value of the relief sought is $10,000 or less, at your request, REJEE Stores will pay all administration and arbitrator fees associated with the arbitration. Any request for payment of fees by REJEE Stores should be submitted by mail to the AAA along with your Demand for Arbitration and REJEE Stores will make arrangements to pay administration and arbitrator fees directly to the AAA. In the event the arbitrator determines the claim(s) you assert in the arbitration to be frivolous, you agree to reimburse REJEE Stores for all fees associated with the arbitration paid by REJEE Stores on your behalf that you otherwise would be obligated to pay under the AAA's rules.")]),_c('li',{staticClass:"pb-3"},[_c('b',[_vm._v("4.\tSeverability")])]),_c('li',[_vm._v("With the exception of any of the provisions in Section 1 of this Agreement to Arbitrate (\"Prohibition of Class and Representative Actions and Non-Individualized Relief\"), if an arbitrator or court decides that any part of this Agreement to Arbitrate is invalid or unenforceable, the other parts of this Agreement to Arbitrate shall still apply.")]),_c('li',{staticClass:"pb-3"},[_c('b',[_vm._v("5.\tOpt-Out Procedure")])]),_c('li',[_c('b',[_vm._v("IF YOU ARE A NEW USER OF OUR SERVICES, YOU CAN CHOOSE TO REJECT THIS AGREEMENT TO ARBITRATE (\"OPT-OUT\") BY MAILING US A WRITTEN OPT-OUT NOTICE (\"OPT-OUT NOTICE\"). THE OPT-OUT NOTICE MUST BE POSTMARKED NO LATER THAN 30 DAYS AFTER THE DATE YOU ACCEPT THE USER AGREEMENT FOR THE FIRST TIME. YOU MUST MAIL THE OPT-OUT NOTICE TO REJEE Stores INC., ATTN: LITIGATION DEPARTMENT, RE: OPT-OUT NOTICE, 583 WEST REJEE Stores WAY, DRAPER, UT 84020.")])]),_c('li',[_vm._v("For your convenience, we are providing an Opt-Out Notice form. You must complete and mail that to us in order to opt out of the Agreement to Arbitrate. You must complete the Opt-Out Notice form by providing the information called for in the form, including your name, address (including street address, city, state and zip code), and the user ID(s) and email address(es) associated with the REJEE Stores Service account(s) to which the opt-out applies. You must sign the Opt-Out Notice for it to be effective. This procedure is the only way you can opt out of the Agreement to Arbitrate. If you opt out of the Agreement to Arbitrate, all other parts of this User Agreement and its Legal Disputes Section will continue to apply to you. Opting out of this Agreement to Arbitrate has no effect on any previous, other, or future arbitration agreements that you may have with us.")]),_c('li',{staticClass:"pb-3"},[_c('b',[_vm._v("6.\tFuture Amendments to the Agreement to Arbitrate")])]),_c('li',[_vm._v("Notwithstanding any provision in the User Agreement to the contrary, you and we agree that if we make any amendment to this Agreement to Arbitrate (other than an amendment to any notice address or site link provided herein) in the future, that amendment shall not apply to any claim that was filed in a legal proceeding against REJEE Stores prior to the effective date of the amendment. The amendment shall apply to all other disputes or claims governed by the Agreement to Arbitrate that have arisen or may arise between you and REJEE Stores. We will notify you of amendments to this Agreement to Arbitrate by posting the amended terms on www.REJEE Stores.co.uk at least 30 days before the effective date of the amendments and by providing notice through the REJEE Stores Message Center and/or by email. If you do not agree to these amended terms, you may close your account within the 30-day period and you will not be bound by the amended terms.")])]),_c('p'),_c('p')])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row product-container pb-3 pt-3"},[_c('div',{staticClass:"content text-dark"},[_c('h3',{staticClass:"text-lg font-weight-bold"},[_vm._v("\n                                     C. Judicial Forum for Legal Disputes\n                                    ")]),_c('p',[_vm._v("All claims or disputes that are not subject to the Agreement to Arbitrate above is found not to apply to you or to a particular claim or dispute, either as a result of your decision to opt out of the Agreement to Arbitrate and all matters brought pursuant to and in aid of the Agreement to Arbitrate will be resolved exclusively by a state or federal court located in Salt Lake County, Utah. You and REJEE Stores agree to submit to the personal jurisdiction of the courts located within Salt Lake County, Utah for the purpose of litigating all such claims, disputes, or matters.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row product-container pb-3 pt-3"},[_c('div',{staticClass:"content text-dark",attrs:{"id":"General"}},[_c('h3',{staticClass:"text-lg font-weight-bold"},[_vm._v("\n                                     19. General\n                                    ")]),_c('p',[_vm._v("Except as otherwise provided in this User Agreement, if any provision of this User Agreement is held to be invalid, void or for any reason unenforceable, such provision shall be struck out and shall not affect the validity and enforceability of the remaining provisions. In our sole discretion, we may assign this User Agreement, and in such event, we will post notice on www.REJEE Stores.co.uk.")]),_c('p',[_vm._v("Headings are for reference purposes only and do not limit the scope or extent of such Section. Our failure to act with respect to a breach by you or others does not waive our right to act with respect to subsequent or similar breaches. We do not guarantee we will take action against all breaches of this User Agreement.\n                                        ")]),_c('p',[_vm._v("We may amend this User Agreement at any time by posting the amended terms on www.REJEE Stores.co.uk. Our right to amend the User Agreement includes the right to modify, add to, or remove terms in the User Agreement. We will provide you 30 days' notice by posting the amended terms. Additionally, we will notify you through the REJEE Stores Message Center and/or by email. Your continued access or use of our Services constitutes your acceptance of the amended terms. We may also ask you to acknowledge your acceptance of the User Agreement through an electronic click-through. This User Agreement may not otherwise be amended except through mutual agreement by you and an REJEE Stores representative who intends to amend this User Agreement and is duly authorized to agree to such an amendment.")]),_c('p',[_vm._v("Without limiting REJEE Stores's ability to refuse, modify, or terminate all or part of our Services, REJEE Stores may also terminate this Agreement with anyone at any time for any reason, at our sole discretion, by giving notice of such termination.")]),_c('p',[_vm._v("The policies and terms posted on our Services may be changed from time to time. Changes take effect when we post them on the REJEE Stores Service.")]),_c('p',[_vm._v("If you create or use an account on behalf of a business entity, you represent that you are authorized to act on behalf of such business and bind the business to this User Agreement. Such account is owned and controlled by the business entity. No agency, partnership, joint venture, employee-employer or franchiser-franchisee relationship is intended or created by this User Agreement.")]),_c('p',[_vm._v("The User Agreement and all terms and polices posted through our Services set forth the entire understanding and agreement between you and REJEE Stores, and supersede all prior understandings and agreements of the parties.")])])])}]

