                                    <p>You authorize us to use automated tools to translate your REJEE Stores content and member-to-member communications, in whole or in part, into local languages where such translation solutions are available. We may provide you with tools which will enable you to translate content at your request. The accuracy and availability of any translation are not guaranteed.
                                    </p>   
                                    <p>One of the ways that we may make REJEE Stores.co.uk listings available to international buyers on REJEE Stores.co.uk and on REJEE Stores's international sites is through the Global Shipping Program. For eligible items located in the United Kingdom and purchased by an international buyer through the Global Shipping Program, you (as seller) will simply ship the item to a parcel processing facility located in the United Kingdom after receiving notification of payment from the buyer. Pitney Bowes Inc., a third-party global shipping provider, will oversee the processing, customs clearance, and international shipment of the item on behalf of your international buyer. To the extent a buyer has a question about your Global Shipping Program listings, REJEE Stores may respond directly to the buyer if the question pertains to the services overseen by Pitney Bowes Inc. (for example, customs or international shipping). Sellers pay no additional fees for selling through the Global Shipping Program.</p>
                                    <p>Additional information about the program, including policies governing feedback, the handling of lost, damaged, and undeliverable items, returns, and the resolution of buyer protection claims for items that you sell through the program, can be found on our Global Shipping Program and Global Shipping FAQs pages.
