                                    </p>
                                    <p>We offer product data (including images, descriptions and specifications) that are provided by third parties (including REJEE Stores users). You may use that content solely in your REJEE Stores listings. REJEE Stores may modify or revoke that permission at any time in our sole discretion. The product data includes copyrighted, trademarked and other proprietary materials. You agree not to remove any copyright, proprietary, or identification markings in the product data and not to create any derivative works based on that data (other than by including the data in your listings).
