                                <div class="row product-container pb-3 pt-3">
                                  <div id="Authorization" class="content text-dark">
                                    <h3 class="text-lg font-weight-bold">
                                     12. Authorization to Contact You; Recording Calls; Analyzing Message Content
                                    </h3>
                                    <p>
                                        REJEE Stores may contact you using autodialed or prerecorded calls and text messages, at any telephone number that you have provided us, to: (i) notify you regarding your account; (ii) troubleshoot problems with your account; (iii) resolve a dispute; (iv) collect a debt; (v) poll your opinions through surveys or questionnaires; or (vi) as otherwise necessary to service your account or enforce this User Agreement, our policies, applicable law, or any other agreement we may have with you. REJEE Stores may also contact you using autodialed or prerecorded calls and text messages for marketing purposes (e.g., offers and promotions), if you consent to such communications. Our collection, use, disclosure, retention, and protection of your personal information is governed by the User Privacy Notice. As described in our User Privacy Notice, REJEE Stores may collect other telephone numbers for you and may place manual non-marketing calls to any of those numbers and autodialed non-marketing calls to any landline. Standard telephone minute and text charges may apply and may include overage fees if you have exceeded your plan limits. If you do not wish to receive such communications, you may change your communications preference at any time, including through the Communication Preferences section of your My REJEE Stores.
                                    </p>
                                    <p>REJEE Stores may share your telephone number with its authorized service providers as stated in our User Privacy Notice. These service providers may contact you using autodialed or prerecorded calls and text messages, only as authorized by REJEE Stores to carry out the purposes identified above.</p>
                                    <p>REJEE Stores may, without further notice or warning and in its discretion, monitor or record telephone conversations you or anyone acting on your behalf has with REJEE Stores or its agents for quality control and training purposes, or for its own protection.</p>
                                    <p>REJEE Stores's automated systems scan and analyze the contents of every message sent through its messages platform, including messages between users, to detect and prevent fraudulent activity or violations of REJEE Stores's User Agreement, including the incorporated terms, notices, rules, and policies. This scanning and analysis may occur before, during, or after the message is sent, or while in storage, and may result in your message being delayed or withheld. REJEE Stores may store message contents, including to conduct this scanning and analysis.</p>
                                    <p>Sellers can create rules to automate replacements, returns, and refunds under certain circumstances. For all new sellers, in listings where returns are accepted, REJEE Stores will set a default rule that automates the return process. Sellers may remove or customize their return preferences in their account settings within My REJEE Stores. Where settings have been set to automatically accept requests for returns or replacements, an REJEE Stores-generated return shipping label will be provided to your buyer. You agree to comply with our returns policy.</p>
                                    <p>When an item is returned, to refund the buyer, you (as seller) authorize REJEE Stores to request that PayPal remove the refund amount (in same or other currency) from your PayPal account, place the amount on your invoice, and/or charge your payment method on file.</p>
                                    <p class="pt-3 pb-3">The cost of return shipping for an item that is not as described is the seller's responsibility.</p>
                                    <p>You (as seller) authorize REJEE Stores to place the return shipping label cost on your invoice, subject to your automatic payment method on file when:</p>
                                    <ul class="p-3 ul list-group">
                                        <li>•	An REJEE Stores-generated return shipping label is used, and the seller is responsible for its cost;</li>
