                                    <p>Most REJEE Stores sales go smoothly, but if there's a problem with a purchase, the REJEE Stores Money Back Guarantee helps buyers and sellers communicate and resolve issues. You agree to comply with the policy and permit us to make a final decision on any REJEE Stores Money Back Guarantee case.</p>
                                    <p>If you (as seller), choose to reimburse a buyer, or are required to reimburse a buyer or REJEE Stores under the REJEE Stores Money Back Guarantee, you authorize REJEE Stores to request that PayPal remove the reimbursement amount (in same or other currency) from your PayPal account, place the amount on your invoice, and/or charge your payment method on file. If we cannot get reimbursement from you, we may collect the outstanding sums using other collection mechanisms, including retaining collection agencies.</p>
