                                <div class="row product-container pb-3 pt-3">
                                  <div id="Managed Payments" class="content text-dark">
                                    <h3 class="text-lg font-weight-bold">
                                     14. Managed Payments
                                    </h3>
                                    <p>REJEE Stores has launched a new managed payments service, where a designated REJEE Stores entity (each, a "payments entity") manages payments on behalf of sellers (such management described as "managed payments" or similar). Some REJEE Stores sellers have already enrolled in managed payments. REJEE Stores anticipates moving more seller accounts to managed payments in phases starting in July 2020, providing notice to the affected sellers as they are scheduled to be moved.</p>
                                    <p>When we enable your account for managed payments, to continue to list and sell on REJEE Stores, as directed by the payments entity, each transitioned REJEE Stores seller registered in the US must:</p>
                                    <ul class="p-3 ul list-group">
                                        <li>•	provide the payments entity with information about you and/or your business to meet its compliance requirements, including those involving identity verification, anti-money laundering controls, and sanctions screening as required by applicable laws and policies; and</li>
                                        <li>•	pass such verification and screening and otherwise meet the compliance requirements of the payments entity, as determined by the payments entity; and</li>
                                        <li>•	provide bank account information for a U.S.-based checking account so that the payments entity can link such checking account to your REJEE Stores account, allowing the payments entity to pay you.</li>
                                    </ul>
                                    <p>In addition:</p>
                                    <ul class="p-3 ul list-group">
                                        <li>•	the payments entity may obtain information about you from third-parties to verify your identity, comply with anti-money laundering and sanctions screening obligations, and for other purposes in connection with managed payments; and</li>
                                        <li>•	the payments entity may use third-party payments service providers to assist it in providing managed payments services, including companies that process payments, perform risk assessments (such as credit agencies) or compliance checks, verify identity, and validate payment methods. REJEE Stores, the payments entity, and their affiliates may send personal data associated with you and your account to such third-parties.</li>
                                    </ul>
                                    <p>The payments entity may, in its sole discretion, manage payments on your behalf even if you haven't provided all requested information, and the payments entity may withhold payouts pending receipt of such information.</p>
                                    <p>The complete terms governing sellers' use of managed payments are available in the Payments Terms of Use, incorporated herein. You agree to the Payments Terms of Use to the extent applicable to you, whether or not your account has been enabled for managed payments.</p>
                                    <p>The contract for sale underlying the purchase of goods is directly concluded between seller and the buyer in the same manner as for transactions for which the payments entity does not manage payments.</p>
                                    <p class="pt-3">If you are a buyer completing a purchase from a seller that is using managed payments:</p>
                                    <ul class="p-3 ul list-group">
                                        <li>•	In certain instances, your transaction may be declined, frozen, or held for any reason including for suspected fraud, AML compliance, compliance with economic or trade sanctions, in connection with REJEE Stores's internal risk controls or due to potential violations of any policy of REJEE Stores or the payments entity, or a policy of one of the payments entity's third party payments services providers.</li>
                                        <li>•	REJEE Stores, the payments entity or its affiliates may save payment information, such as credit card or debit card numbers, and card expiration dates, entered by you on our Services when you make a purchase, redeem a coupon, or make any other transaction on our Services where card information is entered. Such stored payment information may be used as your default payment method for future transactions on our Services. At any time, you can update your card information or enter new card information, at which point the new card information shall be stored as your default payment method. You may make changes to your default payment method through the Personal Information section under the Account tab in My REJEE Stores. You are responsible for maintaining the accuracy of information we have on file, and you consent to REJEE Stores updating such stored information from time to time based on information provided by you, your bank or other payments services providers. You will only provide information about payment methods that you are authorized to use.</li>
                                        <li>•	You may seek returns or cancellations on our Services, or file REJEE Stores Money Back Guarantee claims on our Services, in the same manner as you do for transactions for which the payments entity does not manage payments. The payments entity refunds amounts paid for successful REJEE Stores Money Back Guarantee claims and returned or cancelled transactions in cases where the original payment was managed by the payments entity. Refund timing may vary in accordance with the rules of third parties, such as credit and debit card networks.</li>
                                        <li>•	You agree to comply with, and not cause a third party to violate, all applicable laws, regulations, rules and terms and conditions in connection with the use of managed payments. You understand that some third parties, such as credit and debit card issuers, credit and debit card networks and payments services providers, may have their own terms and conditions for the payment or settlement methods you choose to use in connection with managed payments transactions. Failure to abide by third party terms and conditions may result in fees assessed to you (for example, currency conversion fees from your credit card issuer if the transaction currency is different from your credit card currency) or other actions taken by such third parties, and you agree that the payments entity has no control over, or responsibility or liability for, such fees or actions.</li>
                                        
                                    </ul>
