                                    <h3 class="text-lg font-weight-bold">
                                     A. Applicable Law
                                    </h3>
                                    <p>You agree that, except to the extent inconsistent with or preempted by federal law, the laws of the State of Utah, without regard to principles of conflict of laws, will govern this User Agreement and any claim or dispute that has arisen or may arise between you and REJEE Stores, except as otherwise stated in this User Agreement.
