                                <div class="row product-container pb-3 pt-3">
                                  <div class="content text-dark">
                                    <h3 class="text-lg font-weight-bold">
                                     B. Agreement to Arbitrate
                                    </h3>
                                    <p><b>You and REJEE Stores each agree that any and all disputes or claims that have arisen, or may arise, between you and REJEE Stores (or any related third parties) that relate in any way to or arise out of this or previous versions of the User Agreement, your use of or access to our Services, the actions of REJEE Stores or its agents, or any products or services sold, offered, or purchased through our Services shall be resolved exclusively through final and binding arbitration, rather than in court.
Alternatively, you may assert your claims in small claims court, if your claims qualify and so long as the matter remains in such court and advances only on an individual (non-class, non-representative) basis. The Federal Arbitration Act ("FAA"), and to the extent not inconsistent with the FAA, the laws of the State of Utah, without regard to principles of conflict of laws, governs the interpretation and enforcement of this Agreement to Arbitrate.
</b>
                                    </p>
                                    <ul class="pl-3 pt-3">
                                        <li class="pb-3"><b>1.	Prohibition of Class and Representative Actions and Non-Individualized Relief
                                        <br>YOU AND REJEE Stores AGREE THAT EACH OF US MAY BRING CLAIMS AGAINST THE OTHER ONLY ON AN INDIVIDUAL BASIS AND NOT AS A PLAINTIFF OR CLASS MEMBER IN ANY PURPORTED CLASS, OR REPRESENTATIVE OR PRIVATE ATTORNEY GENERAL ACTION OR PROCEEDING. UNLESS BOTH YOU AND REJEE Stores AGREE OTHERWISE, THE ARBITRATOR MAY NOT CONSOLIDATE OR JOIN MORE THAN ONE PERSON'S OR PARTY'S CLAIMS, AND MAY NOT OTHERWISE PRESIDE OVER ANY FORM OF A CONSOLIDATED, REPRESENTATIVE, CLASS, OR PRIVATE ATTORNEY GENERAL ACTION OR PROCEEDING. ALSO, THE ARBITRATOR MAY AWARD RELIEF (INCLUDING MONETARY, INJUNCTIVE, AND DECLARATORY RELIEF) ONLY IN FAVOR OF THE INDIVIDUAL PARTY SEEKING RELIEF AND ONLY TO THE EXTENT NECESSARY TO PROVIDE RELIEF NECESSITATED BY THAT PARTY'S INDIVIDUAL CLAIM(S). ANY RELIEF AWARDED CANNOT AFFECT OTHER USERS. </b>If a court decides that applicable law precludes enforcement of any of this paragraph's limitations as to a particular claim or a particular request for a remedy (such as a request for injunctive relief), then that claim or that remedy request (and only that claim or that remedy request) must be severed from the arbitration and may be brought in court (pursuant to Section 18.C below), subject to your and REJEE Stores's right to appeal the court's decision. All other claims will be arbitrated.</li>
                                        <li class="pb-3"><b>2.	Arbitration Procedures</b><br>
                                        Arbitration is more informal than a lawsuit in court. Arbitration uses a neutral arbitrator instead of a judge or jury, and court review of an arbitration award is very limited. However, an arbitrator can award the same damages and relief on an individualized basis that a court can award to an individual. An arbitrator should apply the terms of the User Agreement as a court would. All issues are for the arbitrator to decide, except that issues relating to arbitrability, the scope or enforceability of this Agreement to Arbitrate, or the interpretation of Section 1 of this Agreement to Arbitrate ("Prohibition of Class and Representative Actions and Non-Individualized Relief"), shall be for a court of competent jurisdiction to decide.
                                        </li>
                                        <li>The arbitration will be administered by the American Arbitration Association ("AAA") under its rules and procedures, including the AAA's Consumer Arbitration Rules and the AAA's Commercial Arbitration Rules, and the AAA's International Centre for Dispute Resolution Rules (as applicable), as modified by this Agreement to Arbitrate. Absent agreement of the parties, the AAA shall decide which AAA rules apply to the arbitration. The AAA's rules are available at www.adr.org and www.icdr.org. In the event that the AAA is unavailable to administer the arbitration, another administrator will be selected by the parties or, if the parties cannot reach the agreement, the court (pursuant to Section 18.C below) shall select the administrator.
A party who intends to seek arbitration must first send to the other, by certified mail, a valid Notice of Dispute ("Notice"), which may be downloaded at this link. The Notice to REJEE Stores must be sent to REJEE Stores Inc., Attn: Litigation Department, Re: Notice of Dispute, 583 W. REJEE Stores Way, Draper, UT 84020. REJEE Stores will send any Notice to you to the physical address we have on file associated with your REJEE Stores account; it is your responsibility to keep your physical address up to date. To be valid, you must personally sign the Notice and complete all information on the Notice form, including a description of the nature and basis of the claims you are asserting, the specific relief sought, and the email address and phone number associated with your account.
</li>
                                        <li>If you and REJEE Stores are unable to resolve the claims described in a valid Notice within 30 days after REJEE Stores receives that Notice, you or REJEE Stores may initiate arbitration proceedings. A form for initiating arbitration proceedings is available on the AAA's site at www.adr.org. In addition to filing this form with the AAA in accordance with its rules and procedures, the party initiating the arbitration must mail a copy of the completed form to the opposing party. You may send a copy to REJEE Stores at the following address: REJEE Stores, Inc. c/o CT Corporation System, 1108 E South Union Ave., Midvale, UT 84047. In the event REJEE Stores initiates an arbitration against you, it will send a copy of the completed form to the physical address we have on file associated with your REJEE Stores account. Any settlement offer made by you or REJEE Stores shall not be disclosed to the arbitrator.</li>
                                        <li>If you are a resident of the United States, then the arbitration hearing shall be held in the county in which you reside or at another mutually agreed location. If you are not a resident of the United States, then the arbitration hearing will be held in Salt Lake County, Utah, United States, or another mutually agreed location. Where no disclosed claims or counterclaims exceed $25,000, the dispute shall be resolved by the submission of documents only, subject to the arbitrator's discretion to require an in-person hearing, if the circumstances warrant. In cases where an in-person hearing is held, you and/or REJEE Stores may attend by telephone, unless the arbitrator requires otherwise. The language of the arbitration will be English.</li>
                                        <li>The arbitrator will decide the substance of all claims in accordance with applicable law, including recognized principles of equity, and will honor all claims of privilege recognized by law. The arbitrator shall not be bound by rulings in prior arbitrations involving different users, but is bound by rulings in prior arbitrations involving the same REJEE Stores user to the extent required by applicable law. The arbitrator's award shall be final and binding and judgment on the award rendered by the arbitrator may be entered in any court having jurisdiction thereof.</li>
